<template>
  <v-flex xs12 sm6 lg3 pa-3>
    <v-card height="100%" flat elevation=10>
      <!-- Card title -->
      <v-card-title class="headline">
        <v-icon medium class="pr-2">{{info.icon}}</v-icon>
        {{info.header}}
      </v-card-title>

      <!-- item list -->
      <v-list one-line>
        <template v-for="(item) in info.subitems">
          <!-- Items that do have subitems in them -->
          <v-list-group :key="item.name" v-if="item.subitems">
            <!-- main header tile -->
            <template v-slot:activator>
              <v-list-tile
                :key="item.name"
                avatar
                class="tile"
                :class="item.completed ? 'completed' : 'incomplete'"
              >
                <v-list-tile-avatar>
                  <v-avatar
                    :color="completedColor(item.completed)"
                    class="icon_class"
                  >
                    <v-icon>{{item.icon}}</v-icon>
                  </v-avatar>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="item_text">{{item.name}}</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </template>

            <!-- Main subitems for list -->
            <div :key="item.name + '1'" v-if="item.subitems">
              <v-list-tile
                class="tile-subitem"
                v-for="subitem in item.subitems"
                :key="subitem.name"
                :class="subitem.completed ? 'completed' : 'incomplete'"
              >
                <v-list-tile>
                  <v-list-tile-avatar>
                    <v-avatar
                      :color="completedColor(subitem.completed)"
                      class="icon_class"
                    >
                      <v-icon>{{subitem.icon ? subitem.icon : item.icon}}</v-icon>
                    </v-avatar>
                  </v-list-tile-avatar>

                  <v-list-tile-content>
                    <v-list-tile-title>{{subitem.name}}</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </v-list-tile>
            </div>
          </v-list-group>
          <!-- end Items that do have subitems in them -->

          <!-- Items that dont have subitems in them -->
          <v-list-tile
            :key="item.name"
            avatar
            class="tile"
            :class="item.completed ? 'completed' : 'incomplete'"
            v-else
          >
            <v-list-tile-avatar>
              <v-avatar
                :color="completedColor(item.completed)"
                class="icon_class"
              >
                <v-icon>{{item.icon}}</v-icon>
              </v-avatar>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title class="item_text">{{item.name}}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <!-- End Items that dont have subitems in them -->
        </template>
      </v-list>
    </v-card>
  </v-flex>
</template>
<script>
export default {
  props: ["info"],
  methods:
  {
    completedColor(completed)
    {
      var dark = this.$vuetify.dark;
      if(completed)
      {
        if(dark)
          return 'green darken-2';
        return 'green lighten-2';
      }
      if(dark)
        return 'grey darken-2';
      return 'grey lighten-2';
    }
  }

};
</script>

<style scoped>
.tile {
  margin-top: 5px;
}

.tile-subitem {
  margin-top: 5px;
  margin-left: 20px;
}

.icon_class {
  color: green;
}
</style>

